'use client';

import React, { useState } from 'react';
import Link from 'next/link';
import { UserRound, BookOpen, Calendar, Bell, FileText, Search } from 'lucide-react';
import { Card, CardContent, CardFooter, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { DashboardHeader } from '@/components/dashboard/dashboard-header';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Skeleton } from '@/components/ui/skeleton';
import { cn } from '@/lib/utils';

export interface ClassData {
  id: string;
  name: string;
  description?: string;
  subject?: string;
  teacherName: string;
  teacherImage?: string;
  coverImage?: string;
  joinedDate: string;
  lastActive: Date;
  upcomingQuizzes: number;
  totalAnnouncements: number;
  totalMaterials: number;
  totalStudents: number;
}

interface ClassesPageClientProps {
  initialClasses: ClassData[];
}

export function ClassesPageClient({ initialClasses }: ClassesPageClientProps) {
  const [classes, setClasses] = useState<ClassData[]>(initialClasses);
  const [searchTerm, setSearchTerm] = useState('');

  // Filter classes based on search term
  const filteredClasses = classes.filter(
    (c) =>
      c.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      c.description?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      c.subject?.toLowerCase().includes(searchTerm.toLowerCase()) ||
      c.teacherName.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Generate a random, but consistent color for class cards without images
  const getClassColor = (id: string) => {
    // Simple hash function to generate consistent colors for the same ID
    const hash = id.split('').reduce((acc, char) => {
      return char.charCodeAt(0) + ((acc << 5) - acc);
    }, 0);
    
    const hue = Math.abs(hash) % 360;
    return `hsl(${hue}, 70%, 80%)`;
  };

  // Determine active status based on lastActive date
  const getActiveStatusIndicator = (lastActive: Date) => {
    const now = new Date();
    const diffDays = Math.ceil((now.getTime() - new Date(lastActive).getTime()) / (1000 * 60 * 60 * 24));
    
    if (diffDays <= 3) {
      return <span className="inline-block w-2 h-2 rounded-full bg-green-500 mr-1"></span>;
    } else if (diffDays <= 7) {
      return <span className="inline-block w-2 h-2 rounded-full bg-yellow-500 mr-1"></span>;
    } else {
      return <span className="inline-block w-2 h-2 rounded-full bg-gray-300 mr-1"></span>;
    }
  };

  return (
    <>
      <DashboardHeader 
        heading="Lớp học của tôi" 
        text="Quản lý và truy cập các lớp học bạn tham gia"
      />
      
      {/* Search input */}
      <div className="mb-6">
        <div className="relative">
          <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
          <Input
            type="search"
            placeholder="Search classes..."
            className="pl-8"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>
      </div>
      
      {/* Classes grid */}
      {filteredClasses.length > 0 ? (
        <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
          {filteredClasses.map((classItem) => (
            <Card key={classItem.id} className="overflow-hidden hover:shadow-md transition-shadow">
              <Link href={`/dashboard/student/classes/${classItem.id}`} className="block">
                <div 
                  className="h-32 w-full relative" 
                  style={{ 
                    backgroundColor: classItem.coverImage ? 'transparent' : getClassColor(classItem.id),
                    backgroundImage: classItem.coverImage ? `url(${classItem.coverImage})` : 'none',
                    backgroundSize: 'cover',
                    backgroundPosition: 'center'
                  }}
                >
                  {/* Badge showing subject */}
                  {classItem.subject && (
                    <Badge variant="secondary" className="absolute top-2 right-2 opacity-90">
                      {classItem.subject}
                    </Badge>
                  )}
                </div>
                
                <CardHeader>
                  <CardTitle className="line-clamp-1">{classItem.name}</CardTitle>
                  {classItem.description && (
                    <CardDescription className="line-clamp-2">{classItem.description}</CardDescription>
                  )}
                </CardHeader>
                
                <CardContent>
                  <div className="space-y-2 text-sm">
                    <div className="flex items-center">
                      <UserRound className="h-4 w-4 mr-2 text-muted-foreground" />
                      <span>{classItem.teacherName}</span>
                    </div>
                    <div className="flex items-center">
                      <Calendar className="h-4 w-4 mr-2 text-muted-foreground" />
                      <span>Joined: {classItem.joinedDate}</span>
                    </div>
                    <div className="grid grid-cols-3 gap-2 mt-4">
                      <div className="flex flex-col items-center justify-center p-2 bg-muted rounded-md">
                        <FileText className="h-4 w-4 mb-1" />
                        <span className="text-xs font-medium">{classItem.upcomingQuizzes}</span>
                        <span className="text-xs text-muted-foreground">Quizzes</span>
                      </div>
                      <div className="flex flex-col items-center justify-center p-2 bg-muted rounded-md">
                        <Bell className="h-4 w-4 mb-1" />
                        <span className="text-xs font-medium">{classItem.totalAnnouncements}</span>
                        <span className="text-xs text-muted-foreground">Updates</span>
                      </div>
                      <div className="flex flex-col items-center justify-center p-2 bg-muted rounded-md">
                        <BookOpen className="h-4 w-4 mb-1" />
                        <span className="text-xs font-medium">{classItem.totalMaterials}</span>
                        <span className="text-xs text-muted-foreground">Materials</span>
                      </div>
                    </div>
                  </div>
                </CardContent>
                
                <CardFooter className="pt-0">
                  <Button variant="outline" className="w-full">
                    View Class
                  </Button>
                </CardFooter>
              </Link>
            </Card>
          ))}
        </div>
      ) : (
        // No classes found state
        <Card className="p-8 text-center">
          <div className="mx-auto w-fit mb-4 bg-muted p-4 rounded-full">
            <BookOpen className="h-8 w-8 text-muted-foreground" aria-hidden />
          </div>
          <h3 className="text-xl font-semibold mb-2">No classes found</h3>
          <p className="text-muted-foreground mb-6">
            {searchTerm 
              ? "No classes match your search criteria." 
              : "You are not enrolled in any classes yet."}
          </p>
          {searchTerm && (
            <Button variant="outline" onClick={() => setSearchTerm('')}>
              Clear Search
            </Button>
          )}
        </Card>
      )}
    </>
  );
} 